/* before stays, after moves */
.activeCloud1::before, .activeCloud2::before, .activeCloud3::before, .activeCloud4::before, .activeCloud5::before, .activeCloud6::before, .activeCloud7::before, .activeCloud8::before{
  content:"";
  position: absolute;
  top:70%;
  left:50%;
  transform: translate(-50%,-50%);
  width: 50%;   
  height: 10%;
  /* border: 1px solid black; */
  background: url(/bndev/static/media/green.d96d642c.svg) 0%  no-repeat, url(/bndev/static/media/green.d96d642c.svg) 25% no-repeat, url(/bndev/static/media/green.d96d642c.svg) 50% no-repeat, url(/bndev/static/media/green.d96d642c.svg) 75%  no-repeat, url(/bndev/static/media/green.d96d642c.svg) 100%  no-repeat;
  background-size: contain, contain, contain, contain, contain;  
}
.activeCloud1::after, .activeCloud2::after, .activeCloud3::after, .activeCloud4::after, .activeCloud5::after,  .activeCloud6::after, .activeCloud7::after, .activeCloud8::after {
  content:"";
  position: absolute;
  top:70%;
  right:25%;
  transform: translateY(-50%);
  width: 50%;   
  height: 10%;
  /* border: 1px solid black; */
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-animation-name: activeCloud1AfterGoAway;
          animation-name: activeCloud1AfterGoAway;
  -webkit-animation-delay:0s;
          animation-delay:0s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  /* animation-timing-function: steps(3, end); */
  border-radius: 15px 15px 15px 15px;
}

.activeCloud1::after{
-webkit-animation-name: activeCloud1AfterGoAway;
        animation-name: activeCloud1AfterGoAway;
}

.activeCloud2::after{
  -webkit-animation-name: activeCloud2AfterGoAway;
          animation-name: activeCloud2AfterGoAway;
  }
.activeCloud3::after{
-webkit-animation-name: activeCloud3AfterGoAway;
