@-webkit-keyframes rotatingLoader {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }

  50% {
    transform: perspective(120px) rotateX(-360deg) rotateY(360deg);
  }

  100% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
}

@keyframes rotatingLoader {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }

  50% {
    transform: perspective(120px) rotateX(-360deg) rotateY(360deg);
  }

  100% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
}




@-webkit-keyframes tekst2Animation { 
